import React from 'react';
import { EuiPage, EuiFlexGroup, EuiFlexItem } from '@elastic/eui';
// @ts-ignore
import Layout from '../components/layout';

// @ts-ignore
import home_img from '../images/home.png';
import { StaticQuery, graphql } from 'gatsby';
import Img from 'gatsby-image';

/*
 * This component is built using `gatsby-image` to automatically serve optimized
 * images with lazy loading and reduced file sizes. The image is loaded using a
 * `StaticQuery`, which allows us to load the image from directly within this
 * component, rather than having to pass the image data down from pages.
 *
 * For more information, see the docs:
 * - `gatsby-image`: https://gatsby.dev/gatsby-image
 * - `StaticQuery`: https://gatsby.dev/staticquery
 */

const Image = () => (
  <StaticQuery
    query={graphql`
      query {
        placeholderImage: file(relativePath: { eq: "home.png" }) {
          childImageSharp {
            # Other options include height (set both width and height to crop),
            # grayscale, duotone, rotate, etc.
            fixed(width: 1175, height: 1524) {
              # Choose either the fragment including a small base64ed image, a traced placeholder SVG, or one without.
              ...GatsbyImageSharpFixed
            }
          }
        }
      }
    `}
    render={data => {
      return (
        <Img
          className="pageScreenshot"
          alt="Kibana home page"
          fixed={data.placeholderImage.childImageSharp.fixed}
        />
      );
    }}
  />
);

export default () => (
  <Layout>
    <EuiPage>
      <EuiFlexGroup justifyContent="center">
        <EuiFlexItem grow={false}>
          <img
            className="pageScreenshot"
            alt="Kibana home page"
            width={1175}
            src={home_img}
          />
          {/* <Image /> */}
        </EuiFlexItem>
      </EuiFlexGroup>
    </EuiPage>
  </Layout>
);
